.main {
  --column-count: 1;
  display: grid;
  grid-template-columns: repeat(var(--column-count), 200px);
  column-gap: 48px;
  row-gap: 46px;
  justify-content: center;
  padding: 50px 0;

  @media (min-width: 488px) {
    --column-count: 2;
  }

  @media (min-width: 768px) {
    --column-count: 3;
  }

  @media (min-width: 1024px) {
    --column-count: 4;
  }
}
